import React, { Component } from 'react';
import { Button } from 'antd';
import './style.scss';

export default class BtnAddNew extends Component {
  states = '';

  render() {
    const { fileType } = this.props;
    return (
      <Button
        {...this.props}
        className={`btnAction ${this.props.className}`}
        icon={fileType === 'excel' ? 'file-excel' : 'file'}
        size="large"
      >
        {this.props.title}
      </Button>
    );
  }
}

BtnAddNew.defaultProps = {
  className: '',
  title: 'Thm mi',
  fileType: '',
};



